<template>
  <div>
    <el-dialog
      title="角色信息"
      v-model="isshow"
      width="560px"
    >
        <div class="ipt_box">
            <div class="ipt_item_box">
                <span class="text_box">角色名称</span>
                <div class="ipt">
                    <el-input v-model="dataobj.rolename" size="mini"></el-input>
                </div>
                <span class="must">*</span>
            </div>
            <div class="ipt_item_box">
                <span class="text_box">备注</span>
                <div class="ipt">
                    <el-input v-model="dataobj.remark" size="mini"></el-input>
                </div>
                <span class="must"></span>
            </div>
        </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button size="mini" type="primary" @click="isshow = false"
            >保 存</el-button
          >
          <el-button size="mini" @click="isshow = false">取 消</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from "vue";

export default {
  setup() {
      const { proxy } = getCurrentInstance()
    const state = reactive({
        isshow:false,
        dataobj:{
            rolename:'',
            remark:''
        }
    });
    const changestatus = () => {
        proxy.isshow = true
    }
    return {
      ...toRefs(state),
      changestatus
    };
  },
};
</script>
<style scoped>
.ipt_item_box{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    margin-bottom: 10px;
}
.ipt_item_box>.text_box{
    width: 100px;
    margin-right: 10px;
    text-align: right;
}
.ipt_item_box>.must{
    width: 50px;
    margin-left: 10px;
    color: red;
}
.ipt_item_box>.ipt{
    width: calc(100% - 170px);
}
</style>