<template>
  <!-- 角色 -->
  <div class="main_con_box">
    <div class="role_box">
      <div class="role_title">
        <div class="title_name">角色</div>
        <img @click="newadd" src="../../../image/new.png" alt="" />
      </div>
      <div class="role_user">
        <div
          class="user_item"
          :class="user_active == item.id ? 'user_active' : ''"
          v-for="item in userlist"
          :key="item.id"
        >
          <span>{{ item.label }}</span>
          <div class="image_box" v-if="item.id!=1">
              <img style="margin-right:15px" src="../../../image/edit.png" alt="">
              <img src="../../../image/del.png" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="power_list_box">
      <div class="power_top">
        <div class="power_name">主管</div>
        <div class="save_btn">
          <el-button size="mini" type="primary">保存</el-button>
        </div>
      </div>
      <div class="power_item_box">
        <div class="power_item power_menu">
          <div class="power_item_name">菜单</div>
          <div class="power_item_menu_list" :style="{'height':($store.state.innerheight-168)+'px'}">
            <el-tree
                class="eltree"
              :data="$store.state.menulist"
              show-checkbox
              node-key="path"
              :default-expand-all="true"
              :props="defaultProps"
            >
            </el-tree>
          </div>
        </div>
        <div class="power_item power_option">
          <div class="power_item_name">操作权限</div>
          <div class="power_item_option_list" :style="{'height':($store.state.innerheight-168)+'px'}">
            <el-checkbox-group v-model="checkList">
                <el-checkbox class="elchecked" :label="item.label" v-for="item in option_list" :key="item.id"></el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
        <div class="power_item power_data">
          <div class="power_item_name">数据权限</div>
          <div class="power_item_data_list" :style="{'height':($store.state.innerheight-168)+'px'}">
              <div class="radio_item" v-for="item in datalist" :key="item.id">
                  <el-radio v-model="elradio" :label="item.label">{{item.name}}</el-radio>
                  <div class="explain">{{item.explain}}</div>
              </div>
          </div>
        </div>
      </div>
    </div>
    <rolepage ref="rolepage"></rolepage>
  </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from "vue";
import rolepage from './role_detail.vue'

export default {
    components:{ rolepage },
  setup() {
      const { proxy } = getCurrentInstance()
    const state = reactive({
      userlist: [
        { label: "超级管理员", id: 1 },
        { label: "主管", id: 2 },
        { label: "普通用户", id: 3 },
        { label: "财务", id: 4 },
      ],
      option_list:[
          {label:'新增意向客户模块',id:1},
          {label:'新增跟进模块',id:2},
          {label:'提醒模块',id:3},
          {label:'计划模块',id:4}
      ],
      checkList:[],
      datalist:[
          {label:'1',name:'个人',id:1,explain:'只能操作自己的数据'},
          {label:'2',name:'所属部门',id:2,explain:'能操作自己部门员工的数据'},
          {label:'3',name:'所属部门及下级部门',id:3,explain:'能操作自己部门以及下级部门员工的数据'},
          {label:'4',name:'全公司',id:4,explain:'能操作所有人的数据'}
      ],
      elradio:'',
      user_active: 2,
      defaultProps: {
        children: "children",
        label: "title",
      },
    });
    const newadd = () => {
        proxy.$refs.rolepage.changestatus()
    }

    return {
      ...toRefs(state),
      newadd
    };
  },
};
</script>

