img {
  width: 20px;
  height: 20px;
}
.main_con_box {
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.role_box {
  width: 300px;
  height: 100%;
  background: #fff;
  color: #555555;
}
.role_title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 15px;
  box-sizing: border-box;
  font-weight: bold;
}
.user_item {
  padding: 5px 15px 5px 15px;
  box-sizing: border-box;
  color: #555555;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.user_active {
  background: #d2f0fd;
}
.power_list_box{
    width: calc(100% - 310px);
    height: 100%;
    background: #fff;
}
.power_top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    box-sizing: border-box;
    font-weight: bold;
    height: 50px;
    line-height: 50px;
    color: #555555;
}
.power_item_box{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.power_item{
    width: 33.3333%;
}
.power_item_name{
    background: rgba(242, 242, 242, 1);
    height: 46px;
    line-height: 46px;
    font-weight: bold;
    padding: 0 15px;
    box-sizing: border-box;
    color: #555555;
    border: 1px solid rgba(242, 242, 242, 1);
}
.power_item_menu_list,.power_item_option_list,.power_item_data_list{
    border: 1px solid rgba(242, 242, 242, 1);
    overflow-y: auto;
}
.el-checkbox.elchecked{
    margin-right: 0;
    height: 26px;
    width: 100%;
    padding-left: 20px;
    box-sizing: border-box;
}
.radio_item{
    padding-left: 20px;
    box-sizing: border-box;
}
.explain{
    font-size: 12px;
    color: #333;
    opacity: .7;
}
</style>